import "mapbox-gl/dist/mapbox-gl.css";
import React, { useState, useEffect, useCallback } from "react";
import Splash from "./components/Splash";
import Trip from "./components/Trip";
import "./css/app.css";

const App = () => {
  const [data, setData] = useState({});
  const [loaded, setLoaded] = useState(false);

  const requestData = useCallback(async (type) => {
    const res = await fetch(`data/${type}.json`, {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const data = await res.json();
    console.log(data);
    setData((prev) => ({ ...prev, [type]: data }));
  }, []);

  const totalRequest = useCallback(async () => {
    await requestData("trip");
    await requestData("passenger");
    await requestData("not_assigned_passenger");
    await requestData("wait_vehs");
    setLoaded(true);
  }, []);

  useEffect(() => {
    totalRequest();
  }, []);

  return (
    <div className="container">
      {!loaded && <Splash />}
      {loaded && <Trip data={data} />}
    </div>
  );
};

export default App;
